import { syncedStore, getYjsDoc, Box, Y } from "@syncedstore/core";
import { IndexeddbPersistence } from "y-indexeddb";
import { defaultRecipe } from "../utils/defaultRecipe";

export const createRecipe = async (
  id: string,
  name: string
): Promise<Y.Doc> => {
  const doc = new Y.Doc({ guid: id, collectionid: "recipe" });
  const root = doc.getMap("data");

  const recipe = { ...defaultRecipe };
  recipe.name = name;
  const recipeMap = new Y.Map(Object.entries(recipe));

  const ingredients = new Y.Map();
  ingredients.set("fermentable_additions", new Y.Array());
  ingredients.set("hop_additions", new Y.Array());
  ingredients.set("culture_additions", new Y.Array());
  recipeMap.set("ingredients", ingredients);

  root.set("recipe", recipeMap);

  const db = new IndexeddbPersistence(`brewdy-recipes/${id}`, doc);
  await db.whenSynced;

  return doc;
};

export type DataType = {
    recipe: BeerJSON.RecipeType;
}

export const getRecipeDoc = (id: string) => {
  return new Y.Doc({ guid: id, collectionid: "recipe" });
}

export const getRecipeStore = (doc: Y.Doc) => {
  return syncedStore(
    {
      data: {} as DataType,
    },
    doc
  );
};
