const useMap = <T,>(doc: Y.Map<any>, path: string[]): T | null => {
  const [state, setState] = useState<T | null>(null);

  useEffect(() => {
    const f = (events: Y.YEvent<any>[], tx: Y.Transaction) => {
      // Find the element representing the path if it exists
      let current = doc;
      let idx = 0;
      while (idx < path.length) {
        const next = current.get(path[idx]);
        if (next) {
          current = next;
        } else {
          break;
        }
      }

      for (const e of events) {
        let target = e.target;
        let match = false;

        // Check whether the change is in a subtree of the path
        while (target) {
          if (target == current) {
            match = true;
            break;
          }

          target = target.parent;
        }

        // Check wehther the change updates a parent of the path
        let p: AbstractType<any> | null = current;
        while (p) {
          p = p.parent;
          if (p == target) {
            match = true;
            break;
          }
        }

        if (match) {
          const data = _.get(doc.toJSON(), path);
          setState(data);
        }
      }
    };

    setState(_.get(doc.toJSON(), path));
    doc.observeDeep(f);
    return () => doc.unobserveDeep(f);
  }, [doc]);

  return state;
};

const StyleSelector = ({
  recipe,
  onChangeStyle,
}: {
  recipe: BeerJSON.RecipeType;
  onChangeStyle: (id: number) => void;
}) => {
  return (
    <>
      <Select
        label="Style"
        selected={
          recipe.style
            ? { label: recipe.style.name, id: recipe.style.name }
            : null
        }
        options={beerStyles.map((s, idx) => ({
          label: s.name,
          id: idx,
        }))}
        setSelected={(o) => onChangeStyle(o as any)}
      />
    </>
  );
};

