export const RecipeView = () => {
  const { id } = useParams();
  const [doc, setDoc] = useState<Y.Doc | null>(null);
  const [users, setUsers] = useState<string[]>([]);

  useEffect(() => {
    const doc = getRecipeDoc(id!);
    const roomName = `brewdy-recipes/${id}`;
    const db = new IndexeddbPersistence(roomName, doc);
    const webrtc = new WebrtcProvider(roomName, doc);
    setDoc(doc);

    db.whenSynced.then(() => {
      console.log("synced");
    });

    webrtc.awareness.on("update", () => {
      setUsers(
        Array.from(webrtc.awareness.getStates()).map((entry) => entry[1].name)
      );
    });

    webrtc.awareness.setLocalState({
      name: `Alex ${Math.floor(Math.random() * 20)}`,
    });

    const listener = () => {
      awarenessProtocol.removeAwarenessStates(
        webrtc.awareness,
        [doc.clientID],
        "window unload"
      );
    }

    window.addEventListener("beforeunload", listener);

    return () => {
      console.log("disconnecting");
      webrtc.disconnect();
      window.removeEventListener("beforeunload", listener);
      webrtc.destroy();
    };
  }, [id]);

  return doc && <RecipeCreator doc={doc} users={users} />;
};
